y String\n  @if not breakpoint-get('no queries') {\n    @media #{$query-string} {\n      @content;\n    }\n  }\n\n  @if breakpoint-get('no query fallbacks') != false or breakpoint-get('no queries') == true {\n\n    $type: type-of(breakpoint-get('no query fallbacks'));\n    $print: false;\n\n    @if ($type == 'bool') {\n      $print: true;\n    }\n    @else if ($type == 'string') {\n      @if $query-fallback == breakpoint-get('no query fallbacks') {\n        $print: true;\n      }\n    }\n    @else if ($type == 'list') {\n      @each $wrapper in breakpoint-get('no query fallbacks') {\n        @if $query-fallback == $wrapper {\n          $print: true;\n        }\n      }\n    }\n\n    // Write Fallback\n    @if ($query-fallback != false) and ($print == true) {\n      $type-fallback: type-of($query-fallback);\n\n      @if ($type-fallback != 'bool') {\n        #{$query-fallback} & {\n          @content;\n        }\n      }\n      @else {\n        @content;\n      }\n    }\n  }\n\n  @include private-breakpoint-reset-contexts();\n}\n\n\n@mixin mq($query, $no-query: false) {\n  @include breakpoint($query, $no-query) {\n    @content;\n  }\n}\n","\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n@import \"~@/styles/theme/breaks\";\n#StaffList {\n  @include Break( min-width Breaks(2) ){\n    .UiPanel:not(:nth-child(-n+2)) {\n      &:nth-child(odd) {\n        flex-direction: row-reverse;\n        >.UiBox { padding-right: 0 !important }\n        img:only-child { margin-right: 0 }\n      }\n      &:nth-child(even){\n        flex-direction: row;\n        > .UiBox { padding-left: 0 !important }\n        img:only-child { margin-left: 0 }\n      }\n      > .UiBox > img:only-child {\n        // max-height: 32em;\n        width: 100%;\n        max-width: 50vw;\n        align-self: flex-start;\n      }\n    }\n    .UiPanel:last-child > .UiBox {\n      padding-bottom: 0 !important;\n    }\n  }\n  @include Break( max-width Breaks(2) ){\n    .UiPanel:not(:nth-child(-n+2)) .UiBox:first-child {\n      width: 100vw;\n      height: 18em;\n      margin-bottom: 1.5rem;\n      padding: 0;\n      > img:only-child {\n        width: 100%;\n        height: 100%;\n        object-fit: cover;\n        margin: 0;\n      }\n    }\n  }\n  .UiPanel:nth-child(3) .UiBox:first-child {\n    padding-top: 0;\n  }\n}\n"],"sourceRoot":""}]);

