orm: capitalize;\n}  ","article {\n  >*:not(.UiHeading) {\n    &:first-child {\n      margin-top: 0;\n    }\n    &:last-child {\n      margin-bottom: 0;\n    }\n  }\n}","@import \"~@/styles/base/external\";\n@import \"~@/styles/base/doc\";\n\na { color: inherit }","@import \"~@/styles/theme/colors\";\n\n$themes: cream light dark rust !default;\n\n@mixin UI_THEMES_EXTEND( $themes: $themes ){\n  @each $theme in $themes {\n    &_#{$theme} {\n      @extend %#{$theme} !optional;\n      &_translucent {\n        @extend %#{$theme}, %#{$theme}_translucent !optional;\n      }\n    }\n  }\n}\n\n%cream {\n  @extend .UiTheme;\n  color: Color(slate);\n  background: Color(cream);\n}\n%cream_translucent {\n  color: Color(slate);\n  background: rgba(Color(cream),.99);\n}\n\n%light {\n  @extend .UiTheme;\n  color: Color(slate);\n  background: Color(light);\n}\n%light_translucent {\n  color: Color(slate);\n  background: rgba(Color(light),.96);\n}\n\n%dark {\n  @extend .UiTheme;\n  color: Color(silver);\n  background: Color(dark);\n}\n%dark_translucent {\n  color: Color(silver);\n  background: rgba(Color(dark),.99);\n}\n\n%rust {\n  @extend .UiTheme;\n  color: Color(light);\n  background: Color(alt);\n}\n%rust_translucent {\n  color: Color(light);\n  background: rgba(Color(alt),.99);\n}\n\n.UiTheme {\n  & {\n    transition: 0s ease-out;\n  }\n  & { @include UI_THEMES_EXTEND; }\n}","\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n@import '~@/styles/main';\n@import '~@/styles/extend/themes';\n\nimg[src*='preloader'][src*='.gif'] {\n  width: 3rem;\n  height: 3rem;\n  opacity: 0.3;\n}\n\np, li {\n  br {\n    content: \"\";\n    display: block;\n    height: 0;\n    width: 100%;\n    margin: -.5em 0;\n  }\n}\n"],"sourceRoot":""}]);

