 &_translucent {\n        @extend %#{$theme}, %#{$theme}_translucent !optional;\n      }\n    }\n  }\n}\n\n%cream {\n  @extend .UiTheme;\n  color: Color(slate);\n  background: Color(cream);\n}\n%cream_translucent {\n  color: Color(slate);\n  background: rgba(Color(cream),.99);\n}\n\n%light {\n  @extend .UiTheme;\n  color: Color(slate);\n  background: Color(light);\n}\n%light_translucent {\n  color: Color(slate);\n  background: rgba(Color(light),.96);\n}\n\n%dark {\n  @extend .UiTheme;\n  color: Color(silver);\n  background: Color(dark);\n}\n%dark_translucent {\n  color: Color(silver);\n  background: rgba(Color(dark),.99);\n}\n\n%rust {\n  @extend .UiTheme;\n  color: Color(light);\n  background: Color(alt);\n}\n%rust_translucent {\n  color: Color(light);\n  background: rgba(Color(alt),.99);\n}\n\n.UiTheme {\n  & {\n    transition: 0s ease-out;\n  }\n  & { @include UI_THEMES_EXTEND; }\n}","$c-black: #0e0e0e;\n$c-matte: invert($c-black,99.5%);\n$c-theme: #BA9454;\n$c-alt:   #7D1214; // or: #CB3E3D\n\n$colors:(\n  dark:    $c-black, // #0E0E0E\n  slate:   lighten($c-black,15%), // #323232,\n  silver:  lighten($c-black,67%), // #B8B8B8\n  cream:   lighten(desaturate(adjust-hue($c-theme,7deg),14),42), // #F5F3ED,\n  light:   mix( white, $c-matte, 69% ), // #F6F4EE\n  matte:   $c-matte, // #F0F0F0\n  \n  theme:   $c-theme, // #BA9454\n  alt:     $c-alt,\n);\n\n@function Color($key) {\n  $color: map-get($colors,$key);\n  @return $color;\n}","\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n@import '~@/styles/theme/colors';\n@import '~@/styles/theme/fonts';\n@import '~@/styles/extend/themes';\n.AppMenu {\n\n  $Base: &;\n  @extend %cream, %cream_translucent;\n  \n  & { // self\n    position: absolute;\n    top: 100%;\n    right: 0;\n    width: 12em;\n    padding: 0 2em 0 1em;\n    box-shadow: -4px 0 2rem 4px rgba( 48, 41, 3, .08 );\n    border-left: 8px solid rgba(Color(theme), .25);\n  }\n  & { // open\n    opacity: 0;\n    pointer-events: none;\n    &_open {\n      &, & #{$Base} {\n        opacity: 1;\n        pointer-events: auto;\n      }\n    }\n  }\n  &Trigger {\n    $pop: mix( Color(theme), Color(dark), 98% );\n\n    position: relative;\n    color: Color(theme);\n\n    >:first-child {\n      \n      all: unset;\n      appearance: none;\n\n      display: inline-flex;\n      justify-content: center;\n      align-items: center;      \n      \n      padding: .34em .5em;\n      border-radius: 2px;\n      background-color: transparent;\n      transition: .3s 0s ease-out;\n\n      >small {\n        line-height: 1.6;\n        transform: translateY(1px);\n      }\n    }\n    >:first-child:hover,\n    >:first-child:focus-within,\n    &:hover >:first-child {\n      background-color: rgba($pop,.25);\n    }\n    // #AppNav.cream & {\n    //   color: Color(theme);\n    // }\n  }\n  .open,\n  .active {\n    a {\n      font-family: $ff-serif;\n      white-space: nowrap;\n    }\n  }\n  .open a {\n    font-weight: 600;\n    letter-spacing: -.015em;\n  }\n  .active a {\n    font-weight: 900;\n    letter-spacing: -.03em;\n  }\n}\n","\n$ff-sans:  \"futura-pt\", Futura, Verdana, sans-serif;\n\n$ff-serif: \"Butler\", Georgia, serif;\n\n$ff-alt:   nth($ff-serif,2), serif;\n\n$ff-mono:  Source Code Pro, monospace;"],"sourceRoot":""}]);

