g\n  @if not breakpoint-get('no queries') {\n    @media #{$query-string} {\n      @content;\n    }\n  }\n\n  @if breakpoint-get('no query fallbacks') != false or breakpoint-get('no queries') == true {\n\n    $type: type-of(breakpoint-get('no query fallbacks'));\n    $print: false;\n\n    @if ($type == 'bool') {\n      $print: true;\n    }\n    @else if ($type == 'string') {\n      @if $query-fallback == breakpoint-get('no query fallbacks') {\n        $print: true;\n      }\n    }\n    @else if ($type == 'list') {\n      @each $wrapper in breakpoint-get('no query fallbacks') {\n        @if $query-fallback == $wrapper {\n          $print: true;\n        }\n      }\n    }\n\n    // Write Fallback\n    @if ($query-fallback != false) and ($print == true) {\n      $type-fallback: type-of($query-fallback);\n\n      @if ($type-fallback != 'bool') {\n        #{$query-fallback} & {\n          @content;\n        }\n      }\n      @else {\n        @content;\n      }\n    }\n  }\n\n  @include private-breakpoint-reset-contexts();\n}\n\n\n@mixin mq($query, $no-query: false) {\n  @include breakpoint($query, $no-query) {\n    @content;\n  }\n}\n","\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n@import \"~@/styles/theme/breaks\";\n@import \"~@/styles/theme/colors\";\n.WinePage {\n  $B: #{&};\n  &--header {\n    @include Break( (max-width Breaks(4)) (min-width Breaks(2)) ){\n      flex-flow: wrap row !important;\n      > #{$B}--stats {\n        flex: 1 65%;\n        width: 65%;\n      }\n      > #{$B}--media {\n        flex: 1 35%;\n        width: 35%;\n      }\n      >:last-child {\n        min-width: 100%;\n      }\n    }\n    @include Break( min-width Breaks(3) ){\n      // min-height: 90vh;\n    }\n  }\n  // &--stats {}\n  &--media {\n    flex: 0 auto !important;\n    padding-left: 1rem !important;\n    padding-right: 1rem !important;\n    @include Break( max-width Breaks(2) ){\n      order: -1;\n      margin-top: .5rem;\n      margin-bottom: -10rem;\n      padding-top: 6rem !important;\n      background: Color(cream);\n    }\n    img {\n      mix-blend-mode: multiply;\n      max-height: 42em;\n      max-width: 100%;\n      width: auto;\n      height: auto;\n\n      height: auto;\n      // min-height: 75vh;\n      max-height: calc(88vh - 4rem);\n\n      @include Break( max-width Breaks(4) ){\n        max-height: 88vh;\n      }\n      @include Break( max-width Breaks(3) ){\n        max-height: 90vmin;\n      }\n    }\n    @include Break( max-width Breaks(3) ){\n      padding-top: 5rem !important;\n    }\n  }\n  &--intro {\n    @include Break( max-width Breaks(4) ){\n      padding-top: 3.5rem !important;\n      text-align: center;\n    }\n  }\n  // .iconList p {\n  //   margin-top: 1.5em;\n  //   line-height: 1.3;\n  // }\n}\n","$c-black: #0e0e0e;\n$c-matte: invert($c-black,99.5%);\n$c-theme: #BA9454;\n$c-alt:   #7D1214; // or: #CB3E3D\n\n$colors:(\n  dark:    $c-black, // #0E0E0E\n  slate:   lighten($c-black,15%), // #323232,\n  silver:  lighten($c-black,67%), // #B8B8B8\n  cream:   lighten(desaturate(adjust-hue($c-theme,7deg),14),42), // #F5F3ED,\n  light:   mix( white, $c-matte, 69% ), // #F6F4EE\n  matte:   $c-matte, // #F0F0F0\n  \n  theme:   $c-theme, // #BA9454\n  alt:     $c-alt,\n);\n\n@function Color($key) {\n  $color: map-get($colors,$key);\n  @return $color;\n}"],"sourceRoot":""}]);

